import React from 'react';
import { Link } from 'react-router-dom';
import { AiOutlineSearch } from 'react-icons/ai';

export default class Searchproduct extends React.Component{
    constructor(){
        super();
        this.state={
            isLoaded: false,
            error:"",
            _search_product:[]
        }
    }

    componentDidMount(){
        fetch('https://fakestoreapi.com/products')
        .then(res => res.json())
        .then(
            (products) => {
                this.setState({
                    isLoaded: true,
                    _search_product : products
                })
            },
            (error) => {
                this.setState({
                    isLoaded: true,
                    error
                })
            }
        )
    }

    filter(event){
        //var _value = event.target.value;
        var i;
        var input = event.target;
        var filter = input.value.toUpperCase();
        var link_a = document.getElementsByClassName("search-filter-data");
        for (i = 0; i < link_a.length; i++) {
            let txtValue = link_a[i].textContent || link_a[i].innerText;
            if (txtValue.toUpperCase().indexOf(filter) > -1) {
                link_a[i].style.display = "block";
            } else {
                link_a[i].style.display = "none";
            }
        }
    }

    closeFilter(id,event){
        var _input_value = document.getElementById("search-product-ip");
        var link_a = document.getElementsByClassName("search-filter-data");
        for (let i = 0; i < link_a.length; i++) {
            link_a[i].style.display = "none";
        }
        _input_value.value = event.target.innerText.toUpperCase();
        window.location.href = `/products/${id}`;
    }

    render(){
        return(
            <>
                <div className="search-product">
                    <div className="search-input">
                        <span className="search-icon">
                            <AiOutlineSearch />
                        </span>
                        <input onChange={this.filter.bind(this)} type="text" placeholder="Search Product" className="search-ip" id="search-product-ip" /> 
                    </div>
                    <div className="search-drop-down">
                        {
                            this.state._search_product.map((data, index) => 
                                <div key={index}>
                                    <Link onClick={this.closeFilter.bind(this,data.id)} to={`/products/${data.id}`} className="search-filter-data">
                                        {
                                            data.title
                                        }
                                    </Link>
                                </div>
                            )
                        }
                    </div>
                </div>
            </>
        );
    }
}