import React from "react";
import { Link } from "react-router-dom";
import Electronics from '../assets/images/electronics-img.jpg';
import Jewelery from '../assets/images/jewelery.jpeg';
import Menscloth from '../assets/images/mens-clothing.jpg';
import Womenscloth from '../assets/images/womens-clothing.jpg';
import Bannerhero from '../assets/images/banner-hero.png';

export default class Home extends React.Component{
    constructor(){
        super();
        this.state={
            items:[],
            isLoaded:false,
            error:null
        }
    }
    componentDidMount(){
        fetch("https://fakestoreapi.com/products/categories")
        .then(res => res.json())
        .then(
            (result) => {
            this.setState({
                isLoaded: true,
                items: result
            });
            },
            (error) => {
            this.setState({
                isLoaded: true,
                error
            });
            }
        )

        var _input_value = document.getElementById("search-product-ip");
        _input_value.value = "";
    }
    render(){
        const __categories = [Electronics, Jewelery, Menscloth, Womenscloth];
        return(
            <>
                <div className="home-Page-wrapper">
                    <div className="inner-section">
                        <div className="banner-section">
                            <div className="container">
                                <div className="row">
                                    <div className="banner-col col col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                        <h2 className="online-store">Online Store</h2>
                                        <p>
                                            Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.
                                        </p>
                                        <a className="shop-now-btn" href="#product">
                                            Shop Now
                                        </a>
                                    </div>
                                    <div className="banner-hero-col col col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                        <img src={Bannerhero} alt="Banner-Hero" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="container" id="product">
                            <div className="product-section">
                                <div className="row">
                                    {
                                        this.state.items.map((data,index) =>(
                                                <div key={index} className="col col-lg-3 col-md-6 col-sm-12 col-xs-12">
                                                    <div className="categories box">
                                                        <div className="img-section">
                                                            <img src={__categories[index]} alt={data.toUpperCase()} />
                                                        </div>
                                                        <h3 className="categories-head">{data.toUpperCase()}</h3>
                                                        <div className="view-all">
                                                            <Link to={`/product-detail/${data}`} className="view-all-btn">
                                                                View All
                                                            </Link>
                                                        </div>
                                                    </div>
                                                </div>
                                            )
                                        )
                                    } 
                                </div>
                            </div>
                        </div>
                   </div>
               </div>
            </>
        );
    }
}