import React from "react";
import { Link } from "react-router-dom";
import { AiFillHeart, AiOutlineHeart, AiOutlineStar }  from 'react-icons/ai'


export default class Productdetail extends React.Component{
    constructor(){
        super();
        this.state={
            __product_Details:[],
            isLoaded:false,
            error:null,
            _lovite_icon:false,
            url : ""
        }
        
    }

    componentDidMount(){
        var _location = window.location.href;
        var _url = _location.split("/")[4];
        fetch(`https://fakestoreapi.com/products/category/${_url}`)
        .then(res => res.json())
        .then(
            (result) => {
            this.setState({
                isLoaded: true,
                __product_Details: result,
                url: _url.split("%20")
            });
            },
            (error) => {
            this.setState({
                isLoaded: true,
                error
            });
            }
        )
    }

    _loveit(event){
        event.preventDefault();
        this.setState({
            _lovite_icon:!this.state._lovite_icon
        })
    }

    render(){
        return(
            <>
                <div className="product-img">
                   <div className="all-product-page">
                       <div className="container">
                            <div className="row">
                            <div className="navication-to-home col col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                <ul>
                                    <li>
                                        <Link to="/home">Home</Link>
                                    </li>
                                    <li>
                                        <span>/</span>
                                    </li>
                                    <li>
                                        <span>Category /  <strong>{this.state.url}</strong></span>
                                    </li>
                                </ul>
                            </div>
                            {
                                this.state.__product_Details.map((data,index) =>(
                                        <div key={data.id} className="product-each col col-lg-3 col-md-6 col-sm-12 col-xs-12">
                                            <div className="inner-section">
                                                <Link className="product" to={`/products/${data.id}`}>
                                                    <span onClick={this._loveit.bind(this)} className="icon">
                                                        {
                                                            this.state._lovite_icon === false ? <AiOutlineHeart /> : <AiFillHeart />
                                                        }
                                                    </span>
                                                    <span style={{display:"none"}}>{data.id}</span>
                                                    <img className="product-img" src={data.image} alt={data.title} />
                                                    <p className="price"><span className="lable">Price: </span>{data.price}</p>
                                                    <div className="product-rating">
                                                        <p className="rate">
                                                            <span className="rate-no">{data.rating.rate}</span>
                                                            <span className="rate-icon"><AiOutlineStar /></span>
                                                        </p>
                                                        <p className="rating-count">
                                                            <span className="count-no">{data.rating.count} ratings</span>
                                                        </p>
                                                    </div>
                                                    <h6 className="product-name">{data.title}</h6>
                                                </Link>
                                            </div>
                                        </div>
                                    )
                                )
                            }
                            </div>
                        </div>
                   </div>
                </div>
            </>
        );
    }
}