import React from "react";
import { MdOutlineAttachMoney } from 'react-icons/md';
import { AiOutlineStar } from 'react-icons/ai';
import { Link } from "react-router-dom";

export default class Cart extends React.Component{
    constructor(){
        super();
        this.state={
            __single__product_Details:{},
            isLoaded:false,
            error:null,
            _product_rating:{}
        }
    }
    componentDidMount(){
        var _location = window.location.href;
        var _id = _location.split("/")[4];
        fetch(`https://fakestoreapi.com/products/${_id}`)
        .then(res => res.json())
        .then(
            (result) => {
            this.setState({
                isLoaded: true,
                __single__product_Details: result,
                _product_rating : result.rating
            });
            }, 
            (error) => {
            this.setState({
                isLoaded: true,
                error
            });
            }
        )
        var _input_value = document.getElementById("search-product-ip");
        _input_value.value = "";
    }

    add_cart_in_product(){
        this.props.add_cart_count(this.state.__single__product_Details)  
    }

    render(){
        return(
            <>
                <div className="single-product-wrapper">
                    <div className="single-product">
                        <div className="container">
                            <div className="row">
                                <div className="navication-to-home col col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                    <ul>
                                        <li>
                                            <Link to="/home">Home</Link>
                                        </li>
                                        <li>
                                            <span>/</span>
                                        </li>
                                        <li>
                                            <span>Products</span>
                                        </li>
                                    </ul>
                                </div>
                                <div className="product-img-des-col col col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                    <div className="img-section">
                                        <div className="sm-img">
                                            <img src={this.state.__single__product_Details.image} alt={this.state.__single__product_Details.image} />
                                        </div>
                                        <div className="lg-img">
                                            <img src={this.state.__single__product_Details.image} alt={this.state.__single__product_Details.image} />
                                        </div>
                                    </div>
                                </div>
                                <div className="description-col col col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                    <div className="description-section">
                                        <h3 className="category">{this.state.__single__product_Details.category}{this.state.__single__product_Details.id}</h3>
                                        <h2 className="product-name">{this.state.__single__product_Details.title}</h2>
                                        <div className="product-price">
                                            <p className="price-pro">
                                                <span className="lable-icon">
                                                    <MdOutlineAttachMoney />
                                                </span>
                                                <span className="p-price">
                                                    {
                                                        this.state.__single__product_Details.price
                                                    }
                                                </span>
                                            </p>
                                        </div>
                                        <div className="rating">
                                            <div className="product-rating">
                                                <p className="rate">
                                                    <span className="rate-no">{this.state._product_rating.rate}</span>
                                                    <span className="rate-icon"><AiOutlineStar /></span>
                                                </p>
                                                <p className="rating-count">
                                                    <span className="count-no">{this.state._product_rating.count} ratings</span>
                                                </p>
                                            </div>
                                        </div>
                                        <p className="description-p">{this.state.__single__product_Details.description}</p>   
                                    </div>
                                    <div className="add-cart-buy-button">
                                        <button onClick={this.add_cart_in_product.bind(this)} className="add-buy-btn add-cart">Add Cart</button>
                                        <button className="add-buy-btn buy-now">Buy Now</button>
                                    </div>
                                    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}