import { NavLink } from 'react-router-dom';

import Logo from '../assets/images/shopping-bag.png';
import Cart from '../assets/images/shopping-cart.png';
import Profile from '../assets/images/user.png';
import Searchproduct from './Searchproduct';

export default function Header(props){
    return(
        <>
            <header>
                <div className="header-wrapper">
                    <div className="header-inner-section">
                        <div className="logo-Section">
                            <NavLink to="/" className="logo">
                                <img src={Logo} alt="Shopping" />   
                            </NavLink>
                        </div>
                        <div className="navigation-links">
                            <Searchproduct />
                            <nav style={{display:"none"}}>
                                <ul>
                                    <li>
                                        <NavLink className="nav-link" to="/home">
                                            <span>Home</span>
                                        </NavLink>
                                    </li>
                                    <li style={{display:"none"}}>
                                        <NavLink className="nav-link" to="/cart">
                                            <span>Cart</span>
                                        </NavLink>
                                    </li>
                                    <li style={{display:"none"}}>
                                        <NavLink className="nav-link" to="/product-detail">
                                            <span>Product Detail</span>
                                        </NavLink>
                                    </li>
                                    <li style={{display:"none"}}>
                                        <NavLink className="nav-link" to="/profile">
                                            <span>Profile</span>
                                        </NavLink>
                                    </li>
                                </ul>
                            </nav>
                        </div>
                        <div className="cart-profile">
                            <ul>
                                <li>
                                    <NavLink className="link-icons" to="/cart">
                                        <div className="cart-section-header">
                                            <img src={Cart} alt="Cart" />
                                            <span className="cart-lable-sp">Cart</span>
                                            {
                                                props.CartCount?<span id="product-count" className="cart-product-count">{props.CartCount}</span>:null
                                            }
                                        </div>
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink className="link-icons" to="/my-profile">
                                        <img src={Profile} alt="Profile" />
                                    </NavLink>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </header>
        </>
    );
}